import type { Metadata } from "next";
import { Space_Grotesk } from "next/font/google";
import "./globals.css";

const spaceGrotesk = Space_Grotesk({ 
  subsets: ["latin"],
  variable: "--font-space-grotesk",
  weight: ["300", "400", "500", "600", "700"],
});

export const metadata: Metadata = {
  title: "Apex Vendors | Verified Supplier Links",
  description: "Premium Vendor Links and Suppliers for Resellers",
  icons: {
    icon: "/favicon.ico",
  },
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className={`${spaceGrotesk.variable} font-sans antialiased relative bg-background overflow-x-hidden`}>
        {/* 
           Animated Gradients: 
           - Lighter colors (cyan-400, sky-400)
           - Moving animation (custom animate-blob class)
        */}
        
        <div className="fixed inset-0 pointer-events-none z-0 overflow-hidden">
          {/* Top Left Blob */}
          <div className="absolute top-0 left-0 w-[800px] h-[800px] bg-cyan-400/10 rounded-full blur-[120px] mix-blend-screen animate-blob" />
          
          {/* Top Right Blob */}
          <div className="absolute top-0 right-0 w-[800px] h-[800px] bg-sky-400/10 rounded-full blur-[120px] mix-blend-screen animate-blob animation-delay-2000" />
          
          {/* Bottom Left Blob */}
          <div className="absolute -bottom-32 -left-32 w-[800px] h-[800px] bg-blue-400/10 rounded-full blur-[120px] mix-blend-screen animate-blob animation-delay-4000" />
        </div>

        <div className="relative z-10">
          {children}
        </div>
      </body>
    </html>
  );
}
